html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #f7f8fc;
  color: #000;
  font-size: 16px;
}

#snow-effect {
  height: 100vh;
  min-height: 100%;
  background-color: #009688;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.login-card {
  position: absolute;
  left: 50%;
  top: 25%;
  transform: translate(-50%, 0);
  width: 380px;
  font-size: 16px;
}

@media (max-width: 576px) {
  .login-card {
    width: 95%;
  }
}
.vertical-nav {
  min-width: 17rem;
  width: 17rem;
  height: 100vh;
  position: fixed;
  font-size: 15px;
  top: 0;
  left: 0;
  overflow: auto;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
}
.vertical-nav::-webkit-scrollbar {
  width: 5px;
  background-color: #f8f9fa;
}
.vertical-nav::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #ff5722;
}

.page-content {
  width: calc(100% - 17rem);
  margin-left: 17rem;
  transition: all 0.4s;
}

#sidebar.active {
  margin-left: -17rem;
}

#content.active {
  width: 100%;
  margin: 0;
}

.separator {
  margin: 3rem 0;
  border-bottom: 1px dashed #fff;
}

.text-uppercase {
  letter-spacing: 0.1em;
}

.text-gray {
  color: #aaa;
}

.nav-link {
  text-transform: capitalize;
}
.nav-link:hover {
  background: #f3f2f2;
}

.text-primary {
  color: #3f51b5 !important;
}

.sidebar-accessory {
  font-size: 22px;
}

.sidebar-close {
  position: fixed;
  width: 50px;
  height: 50px;
  z-index: 342;
  top: 50%;
  left: -5rem;
  background-color: #009688;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.8s;
}
.sidebar-close .fa-chevron-left {
  font-size: 26px;
  margin-top: 28%;
}

.block {
  display: block;
}

@media (max-width: 992px) {
  #sidebar {
    margin-left: -17rem;
    z-index: 54;
  }
  #sidebar.active {
    margin-left: 0;
    z-index: 55;
  }
  #content {
    width: 100%;
    margin: 0;
  }
  .sidebar-close {
    left: -5rem;
  }
  .sidebar-close.active {
    left: 17rem;
  }
}
.card.no-bg {
  background-color: transparent;
}

.card.dashboard .card-body i {
  position: absolute;
  z-index: 0;
  top: 15px;
  right: 5%;
  font-size: 3rem;
}
.card.dashboard .card-body i:hover {
  animation-name: icon-hover;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
}

@keyframes icon-hover {
  from {
    font-size: 3rem;
  }
  to {
    font-size: 3.5rem;
  }
}
.card-border-bottom {
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 5px solid #29b6f6;
}

.table-index {
  border-top: 4px solid #e64a19;
}

.href-row {
  cursor: pointer;
}

@media (max-width: 992px) {
  .lg-mt-2 {
    margin-top: 0.5rem;
  }
}
.custom-rounded {
  border-radius: 20px;
}

.custom-link {
  text-decoration: none;
  color: #e64a19;
}
.custom-link:hover {
  color: #e64a19;
  text-decoration: none;
  text-shadow: 1px 2px 20px #e64a19;
}

ol.show-gracefully > li {
  opacity: 0;
}

ol.show-gracefully.is-ready > li {
  transition: opacity 0.5s ease-in;
  opacity: 1;
}

.btn-menu {
  background-color: #fff;
  color: #ff5722;
  border-radius: 0;
  margin-left: 20px;
}
.btn-menu:hover {
  color: #fff;
  background-color: #ff5722;
  animation-name: btn-hover;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.btn-add-new {
  background-color: #29b6f6;
  color: #fff;
  border-radius: 0px;
}
.btn-add-new:hover {
  color: #29b6f6;
  background-color: #fff;
  border-color: #29b6f6;
  animation-name: btn-styled;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.btn-quick-back {
  background-color: #3f51b5;
  color: #fff;
  border-radius: 0;
}
.btn-quick-back:hover {
  color: #3f51b5;
  background-color: #fff;
  border-color: #3f51b5;
  animation-name: btn-styled;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.btn-primary {
  color: #fff;
  background-color: #ff5722;
  outline: 0;
  border: 0px;
}
.btn-primary .focus {
  color: #fff;
  background-color: #ff5722;
  border: 1px solid #ff5722;
  box-shadow: none;
}
.btn-primary:focus {
  color: #fff;
  background-color: #ff5722;
  border: 1px solid #ff5722;
  box-shadow: none;
}
.btn-primary:hover {
  background-color: #fff;
  border: 1px solid #ff5722;
  color: #ff5722;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 87, 34, 0.2039215686);
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #ff5722;
  border-color: #ff5722;
}

.btn-show {
  color: #fff;
  background-color: #3f51b5;
  outline: 0;
}
.btn-show:hover {
  color: #3f51b5;
  background-color: #fff;
  border: 1px solid #3f51b5;
}

.btn-edit {
  color: #fff;
  background-color: #00897b;
  outline: 0;
}
.btn-edit:hover {
  color: #00897b;
  background-color: #fff;
  border: 1px solid #00897b;
}

.btn-delete {
  color: #fff;
  background-color: #e91a2e;
  outline: 0;
}
.btn-delete:hover {
  color: #e91a2e;
  border: 1px solid #e91a2e;
  background-color: #fff;
}

@keyframes btn-hover {
  0% {
    border-radius: math(20px, 4);
  }
  50% {
    border-radius: math(20px, 2);
  }
  100% {
    border-radius: 20px;
  }
}
@keyframes btn-styled {
  0% {
    border-radius: 0;
  }
  25% {
    border-radius: math(20px, 4);
  }
  50% {
    border-radius: math(20px, 3);
  }
  75% {
    border-radius: math(20px, 2);
  }
  100% {
    border-radius: 20px;
  }
}
.text-1 {
  color: #ff5722;
}

.text-2 {
  color: #e64a19;
}

.text-3 {
  color: #29b6f6;
}

.text-4 {
  color: #3f51b5;
}

.text-5 {
  color: #009688;
}

.text-6 {
  color: #00897b;
}

.text-size {
  font-size: 2.5rem;
}

.initial {
  overflow-wrap: initial;
}